<template>
  <div class="admin">
    <v-container>
      <h2></h2>

      <p class="admin-warning">
        
      </p>
      <div
        ref="uploadContainer"
        class="admin-upload"

        @click="uploadClick"
        @drop.prevent="uploadDrop"
        @dragover.prevent
        @dragleave.prevent
      >
        <img src="@/assets/excel.png" alt="" class="admin-upload-img">
        <span class="admin-upload-text admin-warning">EXCEL</span>
        <input ref="upload" class="admin-upload-file" type="file" name="file" @change="uploadSubmit">
      </div>

      <hr class="more-margin">
      <p class="admin-warning">
        
      </p>
      <v-data-table
        :headers="headers"
        :items="excelData"
        class="elevation-1"
        hide-actions
      >
        <template v-slot:items="props">
          <td class="text-xs-right">
            {{ props.item.group_rank }}
          </td>
          <td class="text-xs-right">
            {{ props.item.group }}
          </td>
          <td class="text-xs-right">
            {{ props.item.name }}
          </td>
          <td class="text-xs-right">
            {{ props.item.team }}
          </td>
          <td class="text-xs-right">
            {{ props.item.track }}
          </td>
          <td class="text-xs-right">
            {{ props.item.performance }}
          </td>
          <td class="text-xs-right">
            {{ props.item.no }}
          </td>
          <td class="text-xs-right">
            {{ props.item.remarks }}
          </td>
        </template>
      </v-data-table>

      <hr class="more-margin">
      <p class="admin-warning">
        
      </p>
      <v-btn color="success" :loading="uploading" @click="handleUpload">
        
      </v-btn>

      <h2></h2>
      <v-switch
        v-model="openAdvanced"
        :label="openAdvanced ? '' : ''"
      />
      <div v-if="openAdvanced">
        <p></p>
        <p class="admin-warning">
          
        </p>
        <v-layout v-for="arg in args" :key="arg.key">
          <v-flex
            xs12
            md6
          >
            <v-text-field
              v-model.number="vector[arg.key]"
              :label="arg.description"
              required
            />
          </v-flex>
          <v-flex
            xs12
            md6
          >
            Excel{{ getColumn(vector[arg.key]) }}
          </v-flex>
        </v-layout>
      </div>
    </v-container>
  </div>
</template>

<script>
import api from '@/api'
import xlsx from 'xlsx'
import swal from 'sweetalert'

function asyncReadFile(file) {
  return new Promise((resolve, reject) => {
    const reader = new FileReader()
    reader.onload = e => {
      const data = new Uint8Array(e.target.result)
      resolve(data)
    }
    reader.onerror = error => {
      reject(error)
    }
    reader.readAsArrayBuffer(file)
  })
}

export default {
  name: 'Admin',
  data() {
    return {
      args: [
        {
          key: 'group_rank',
          description: ''
        },
        {
          key: 'group',
          description: ''
        },
        {
          key: 'name',
          description: ''
        },
        {
          key: 'team',
          description: ''
        },
        {
          key: 'track',
          description: ''
        },
        {
          key: 'performance',
          description: ''
        },
        {
          key: 'no',
          description: ''
        },
        {
          key: 'remarks',
          description: ''
        }
      ],
      headers: [
        {
          text: '',
          value: 'group_rank'
        },
        {
          text: '',
          value: 'group'
        },
        {
          text: '',
          value: 'name'
        },
        {
          text: '',
          value: 'team'
        },
        {
          text: '',
          value: 'track'
        },
        {
          text: '',
          value: 'performance'
        },
        {
          text: '',
          value: 'no'
        },
        {
          text: '',
          value: 'remarks'
        }
      ],
      vector: {
        group_rank: 0,
        group: 1,
        name: 2,
        team: 3,
        track: 4,
        performance: 5,
        no: 6,
        remarks: 7
      },
      header: [],
      excelData: [],
      openAdvanced: false,
      uploading: false
    }
  },
  methods: {
    uploadClick() {
      this.$refs.upload.click()
    },
    uploadSubmit() {
      const file = this.$refs.upload.files[0]
      if (file) {
        this.uploadHandle(file)
      }
    },
    uploadDrop(evt) {
      const file = evt.dataTransfer.files[0]
      if (file) {
        this.uploadHandle(file)
      }
    },
    async uploadHandle(file) {
      if (!file.name.endsWith('xlsx')) {
        swal('Excelxlsx')
        return
      }
      try {
        const fileData = await asyncReadFile(file)
        const workbook = xlsx.read(fileData, { type: 'array' })
        const firstWorksheet = workbook.Sheets[workbook.SheetNames[0]]
        const data = xlsx.utils.sheet_to_json(firstWorksheet, { header: 1 })
        this.header = data[0]
        this.excelData = this.parseData(data)
      } catch (error) {
        swal('' + error.message)
      }
    },
    parseData(data) {
      const result = []
      for (let i = 1; i < data.length; i++) {
        const line = data[i]
        const item = { ...this.vector }
        for (const k in this.vector) {
          const index = this.vector[k]
          item[k] = line[index]
        }
        result.push(item)
      }
      return result
    },
    getColumn(index) {
      const text = this.header[index]
      const columnName = String.fromCharCode(65 + index)
      return `${columnName} ${text}`
    },
    async handleUpload() {
      this.uploading = true
      try {
        await api.uploadGameDetails(this.excelData)
        this.excelData = []
        swal('', '', 'success')
      } catch (error) {
        swal('' + error.message)
      }
      this.uploading = false
    }
  }
}
</script>

